import React from 'react'
import { Link } from 'react-router-dom'

import Button from './Button'
import Darkmode from './Darkmode'
import Icon from './Icon'

const Menu = () => {

  const turnMenu = () => {
    document.getElementById('menu').classList.remove('hide')
  }

  return(
    <div className="menu" id="menu">
      <section className="menu__close">
        <h1>
          Configuración
        </h1>
        <Button
          onClick={() => turnMenu()}
          typeStyle="secondary"
          name="X"
        />
      </section>
      <section className="menu__nav">
        <h2>
          Navegación
          <Icon
            type="exit"
            setColor="fff"
            setMargin="0 10px"
          />
        </h2>
        <Link
          onClick={() => turnMenu()}
          to="/top-line"
        >
          Top Line
        </Link>
        <Link
          onClick={() => turnMenu()}
          to="/bottom-line"
        >
          Bottom Line
        </Link>
      </section>
      <section className="menu__profile">
        <h2>
          Cuenta
          <Icon
            type="profile"
            setColor="fff"
            setMargin="0 10px"
          />
        </h2>
        <Link
          onClick={() => document.body.classList.remove('dark')}
          to="/login"
        >
          Mi perfil
        </Link>
        <Link
          onClick={() => document.body.classList.remove('dark')}
          to="/login"
        >
          Cerrar Sesión
        </Link>
      </section>
      <section className="menu__darkmode">
        <h2>Modo oscuro</h2>
        <div>
          <p>
            Modo oscuro
          </p>
          <Darkmode />
        </div>
      </section>
    </div>
  )
}

export default Menu