import React from 'react'
import { Link } from 'react-router-dom'

import ProfileDemo from '../assets/profile.jpg'
import Icon from './Icon'

const Profile = () => {
  return (
    <>
      <div className="profile">
        <img
          src={ProfileDemo}
          alt='profile-img'
        />
        <Icon
          type="down"
          setColor="202020"
        />
        <nav className="profile__menu">
          <Link to='/login'>
            <Icon
              type="profile"
              setColor="fff"
              setMargin="0 10px"
            />
            Mi perfil
          </Link>
          <Link to='/login' onClick={() => document.body.classList.remove('dark')}>
            <Icon
              type="exit"
              setColor="fff"
              setMargin="0 10px"
            />
            Cerrar sesión
          </Link>
        </nav>
      </div>
    </>
  )
}

export default Profile
