import React from 'react'
import Loader from '../Loader';

import ShapeBarChart from './ShapeBarChart'
import BarChartc from './BarChart'

const TopLineGraphs = ({data}) => {

  // console.log(data)
  const getSalesPerCode = () => data
  // .map(item => item.Codigo)
  .map(item => ({
      Codigo: item.Codigo,
      // Fecha: item.Fecha,
      Ventas: item.Ventas,
      // Estado: item.Estado
  }))
  .reduce((obj, item) => {
    if(obj[item.Codigo]) {
      obj[item.Codigo] = obj[item.Codigo] + parseInt(item.Ventas);
    } else {
      obj[item.Codigo] = parseInt(item.Ventas);
    }
    return obj;
  }, {});

  const getDataForGraphs = () => {
    const moreSalesData = getSalesPerCode()
    const arrayData = Object.keys(moreSalesData).map(key => ({code: key, sales: moreSalesData[key]}));
    console.log('moreSalesData', moreSalesData)
    console.log('arrayData', arrayData)
    // return <Loader />
    return (
      <>
      <h2>¿En dónde hubo mayores ventas?</h2>
        <ShapeBarChart data={arrayData}/>
        <BarChartc data={arrayData}/>
      </>
    )
  }
  return (
    <>
    {getDataForGraphs()}
    </>
  )
}

export default TopLineGraphs


